<template>
  <v-container>
    <v-row>
      <v-col>
        <h3>Вопрос {{ currentQuestion + 1}} из 25</h3>
      </v-col>
      
      <v-col cols="12">
        <v-img
          v-if="picture"
          :src="require(`../assets/pictures/${picture}`)"
          class="my-3"
          contain
          height="200"
        />
      </v-col>

      <v-col>
        <h2 class="display-1 font-weight-bold mb-3">
          {{ question }}
        </h2>
      </v-col>

      <v-col
        cols="12"
      >
        <QuestionAnswer
          justify="center"
          v-for="(answer, i) in answerList"
          :answerData="answer"
          :key="i"
          :id="id"
          @click="clickAnswer"
        />
      
      </v-col>

      <v-col v-if="tipVisible">
        <div class="alert-tip">
          <h3>Неверно, вот тебе подсказка:</h3>
          <p>
            {{ tip }}
          </p>
        </div>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import QuestionAnswer from './QuestionAnswer.vue';

export default {
  name: 'QuestionCard',

  props: {
    currentQuestion: {
      type: Number,
    },
    questionData: {
      type: Object,
      required: true
    }
  },

  data() {
    return {
      tipVisible: false,
    }
  },

  methods: {
    clickAnswer(param) {
      if (param) {
        this.$emit('nextQuestion');
      } else {
        this.$emit('showFalse');
        this.showTip();
      }
    },
    showTip() {
      this.tipVisible = true;
    },
    hideTip() {
      this.tipVisible = false;
    }
  },

  computed: {
    question() {
      return this.questionData.question;
    },
    answerList() {
      return this.questionData.answerList;
    },
    picture() {
      return this.questionData.picture;
    },
    tip() {
      return this.questionData.tip;
    },
    id() {
      return this.questionData.id;
    }
  },

  watch: {
    id() {
      this.hideTip();
    }
  },

  components: {
    QuestionAnswer
  }
}
</script>


<style lang="scss">
.alert-tip {
  background-color: #B3E5FC;
  padding: 20px;
  border-radius: 5px;
}
</style>