<template>
  <v-container>
    <v-row class="text-center">
      <v-col>
        <router-link to="/" class="ma-3">К списку билетов</router-link>
      </v-col>
    </v-row>
    
    <v-col>
      <h1>Билет {{ id }}</h1>
      
      <v-col v-if="finished">
        <p>
          Молодец! <br/>
          Ты правильно ответил на {{ results.filter(r => r === '+').length }} из {{ results.length }} вопросов.
        </p>
      </v-col>

      <QuestionCard 
        v-else
        :currentQuestion="currentQuestion"
        :question-data="questionData"
        @nextQuestion="nextQuestion"
        @showFalse="setResult(false)"
      />
    </v-col>
    
  </v-container>
</template>

<script>
import QuestionCard from '@/components/QuestionCard.vue';

import QUESTION_LIST from '@/data/questions';
import COUNT_QUESTION from '@/data/count_question';

export default {
    name: "ExamPaperView",
    props: {
        id: {
            type: String
        }
    },

    data() {
        return {
            currentQuestion: 0,
            results: [null],
            finished: false,
        };
    },

    methods: {
      nextQuestion() {
        this.setResult(true); 

        if (this.examQuestionList.length <= this.currentQuestion + 1) {
          this.finished = true;
        } else {
          this.$nextTick(() => {
            this.currentQuestion++;
            this.results.push(null);
          })
        }
      },
      setResult(param) {
        if (!this.results[this.currentQuestion]) {
          this.results[this.currentQuestion] = param ? '+' : '-';
        }
      }
    },

    computed: {
        questionList() {
            return QUESTION_LIST;
        },
        examQuestionList() {
          let startPos = (Number(this.id) - 1) * COUNT_QUESTION;
          let finishPos = (Number(this.id) + COUNT_QUESTION - 1) * COUNT_QUESTION;
          return this.questionList.slice(startPos, finishPos);
        },
        questionData() {
          return this.examQuestionList[this.currentQuestion];
        }
    },

    components: { QuestionCard }
};

</script>
