<template>
    <div block class="question-answer" :class="classes" @click="clickAnswer">
        {{ text }}
    </div>
</template>

<script>
export default {
  name: 'QuestionAnswer',

    props: {
        answerData: {
            type: Object,
            required: true
        },
        id: {
            type: [String, Number]
        }
    },

    data() {
        return {
            error: false,
            success: false,
        }
    },

    methods: {
        clickAnswer() {
            if (!this.param) {
                this.error = true;
            } else {
                this.success = true;
            }

            this.$emit('click', this.param);
        },
        clear() {
            this.error = false;
            this.success = false;
        }
    },

    computed: {
        text() {
            return this.answerData.text;
        },
        param() {
            return this.answerData.param;
        },
        classes() {
            return {
                'error': this.error,
                'success': this.success
            }
        }
    },

    watch: {
        id() {
            this.clear();
        }
    }
}
</script>

<style lang="scss">
.question-answer {
    padding: 20px;
    margin-bottom: 20px;
    background-color: #ECEFF1;
    border-radius: 5px;
    cursor: pointer;

    &.error {
        background-color: #FFCDD2;
    }
}
</style>